import Vue from 'vue'
import Router from 'vue-router'

import ModalExample from 'pages/ModalExample'
import TableTemplate from 'pages/TableTemplate'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  routes: [
    { path: '/modal', component: ModalExample },
    { path: '/table', component: TableTemplate }
  ],
  linkActiveClass: 'active'
})



